    @Mutation SET_MAP_KEY() {this.map_key++}
    @Mutation UPDATE_GEO_DATA(payload: Array<Object>) {
        window.console.log('updating markers')
        window.console.log(payload)
        this.markers = payload
    }
    @Mutation ADD_TO_ICON_IMAGES(payload: string){
      this.icon_images.push(payload);
    }

    @Mutation ADD_MARKER(payload: Object){
      this.markers.push(payload);
      this.marker_counter++ 
      window.console.log('this.marker_counter: ' + this.marker_counter)
    }

    @Mutation SET_CURRENT_TILE(payload: Object){
      this.current_tile = payload;
    }
    /*
    @Mutation ADD_USER_DATA: (state, data: Object) => {
        state.userData.push(data: Object)
      }

    */

    @Mutation SELECTED_ICON(payload: number){
      this.selected_icon = payload
    }

    @Mutation CUSTOMIZED_ICON(payload: string){
      this.customized_icon = payload
    }

    @Mutation SET_MAP_CENTER(payload: Array<number>){
      this.map_center = payload
    }

    @Mutation SET_ICONS(){
      window.console.log('getting icons')
      axios({
        method: 'get',
        url: this.base_url + '/get_icon'
   
    }
            
  ).then(
    response => {
      let response_array: Array<Object> = []
      window.console.log(response)
      
      window.console.log('response length: ' + response.data.length)
      window.console.log('response item: ' + response.data[0].id)
      
      response.data.forEach(function(item: Object){response_array.push(item)});   
      window.console.log(response_array);


    return this.icons = response_array
    }
    )


      
    }


